.login-form-container {
    max-width: 24rem;
    padding: 39px;
    margin: auto;
}

.login-form-container form {
    padding: 20px 20px;
    width: 100%;
    box-sizing: border-box;
    border-radius: 12px;
}

.login-form-container form h2 {
    text-align: center;
    color: #fff;
    margin: 6px 0;
}

.login-form-container form button {
    width: 100%;
    padding: 11px 0;
    margin: 12px 0px;
}

.login-form-container form p {
    color: #fff;
    font-size: 14px;
    margin-bottom: 6px;
    text-align: center;
}

.login-form-container .login-link {
    margin-bottom: 12px;
}

.login-form-container form a {
    color: rgb(219, 219, 219);
    font-size: 14px;
    text-decoration: none;
}


.login-form-container form a:hover {
    text-decoration: underline;
}
