.header {
    padding: 5px 0.5rem;
    box-shadow: 2px 2px 2px 2px rgba(1, 1, 1, 0.16);
    width: 100%;
    position: fixed;
}

.header .header-container {
    max-width: 72rem;
    display: flex;
    flex-direction: row;
    align-content: stretch;
    justify-content: center;
    margin: 0 auto;
}

.header .header-container .header-navigation {
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    float: left;
    align-items: center;
}

.header .header-container .header-navigation li {
    padding: 0 0 0 16px;
    float: left;
    display: inline;
}

.header .header-container .header-navigation li a {
    text-decoration: none;
    color: white;
    font-size: 15px;
    transition: 0.5s;
}

.header .header-container .header-navigation li a:hover {
    text-decoration: underline;
}

.v-separate {
    position: inherit;
    height: 60%;
    margin-left: 10px;
    padding: 1px;
    background-color: white;
    opacity: 0.5;
}

.h-separate {
    position: absolute;
    width: 200px;
    margin: auto 0;
    margin-left: 10px;
    padding: 1px;
    background-color: white;
    opacity: 0.5;
}

button {
    color: white;
    background-color: attr(primary-color);
    border-style: double;
    border: 0.25rem;
    padding: 6px 12px;
    border-radius: 0.25rem;
    transition: 0.5s;
    cursor: pointer;
}

button[buttontype=success] {
    background-color: #28a745;
    border-color: #28a745;
}
button[buttontype=success]:hover {
    background-color: #1b7430;
}

button[buttontype=primary] {
    background-color: #007bff;
    border-color: #007bff;
}
button[buttontype=primary]:hover {
    background-color: #0253aa;
}

.header .header-container .header-navigation li button[buttonType=danger] {
    background-color: #dc3545;
    border-color: #dc3545;
}
.header .header-container .header-navigation li button[buttonType=danger]:hover {
    background-color: #c7303f;
}

.header .header-container .header-logo {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header .header-container .header-logo img {
    display: block;
    width: 186px;
    float: left;
}
.footer {
    padding: 2rem 0px;
    display: flex;
    flex-direction: column;
    align-content: center;
    text-align: center;
}

.footer .footer-social-link {
    margin-bottom: 1vw;
}

.footer .footer-social-link .social-container {
    height: 100%;
}

.footer .footer-social-link .social-container img {
    max-height: 3vw;
    display: inline-block;
    margin: 0px 1vw;
    transition: 0.5s;
    opacity: 0.5;
}

.st0 {
    color: #dc3545;
    background-color: #dc3545;
}

.footer .footer-social-link .social-container img:hover {
    opacity: 1;
}

.footer .footer-copyright {
    text-align: center;
    opacity: 0.5;
}
